			'image': 'img/endorsers/meifang-zhang.jpg',
			'company': 'Deputy Consul General\nConsulate General of China in New York',
			'description': 'Through their music, this orchestra will promote mutual understanding and friendship between the peoples of China and the United States. Their art will transcend national boundaries, races, religions and cultures.'
