		'about_data': [{
			'title': 'TRAINING RESIDENCY',
			'image': 'img/about-w-conservatory.jpg',
			'description': 'The training residency of NYO-China is designed to develop individual members skills as musicians and to prepare the orchestra as a whole for its concert tour. Members of the orchestra will participate in a two-week program that consists of sectional rehearsals led by a world-class faculty of coaches and full orchestra rehearsals under the direction NYO-Chinas music director and guest conductor. Members will also have access to other learning and exchange opportunities such as master classes, recreational chamber music readings, weekend concert excursions, and social interactions with NYO-USA.',
			'align': 'left'
		}, {
			'title': 'CONCERT TOUR',
			'image': 'img/about-concert-tour.jpg',
			'description': 'NYO-Chinas concert tour will last for one week immediately following its debut performance in Carnegie Hall on July 22nd. Upon arrival in China, the orchestra will perform in premier venues throughout the country, including the National Centre for the Performing Arts (NCPA) in Beijing, the Shanghai Grand Theatre, and the Guangzhou Opera House. This tour will showcase the orchestra on an international level and allow NYO-Chinas musicians to expand the reach of this remarkable cultural exchange to audiences at home.',
			'align': 'right'
		}],
