import React from 'react'
import {BigIcons, BigIcon} from '../BigIcons'

import './tuition-free.scss'

export default class TuitionFree extends React.Component {
	shouldComponentUpdate() {
		return false
	}
	render() {
		return (
			<div className="tuition-free">
				<h2 className="middle-title">Tuition Free</h2>
				<div className="middle-subtitle">Ensuring music education opportunities for everyone.</div>
				<BigIcons full>
					<BigIcon icon="education">Education</BigIcon>
					<BigIcon icon="travel">Travel</BigIcon>
					<BigIcon icon="housing">Housing</BigIcon>
				</BigIcons>
				<p className="gray-text">Because great artists can come from anywhere, every talented and hardworking young musician will have an equal chance of participating in NYO-China. Due to the generous support of the orchestras sponsors, no admitted student will ever be turned away due to cost. All program fees  including those related to education, travel, and housing  will be entirely covered.</p>
			</div>
		)
	}
}


